.loader{
    width: 75px;
    height: 75px;
    border-radius: 50%;
    border: 3px dashed rgb(152, 210, 140);
    animation: Rotate 2s infinite linear;
}

@keyframes Rotate{
    0%{
        transform: rotate(0deg) scale(1);
    }
    50%{
        transform: rotate(180deg) scale(0.5);
    }
    100%{
        transform: rotate(360deg) scale(1);
    }
}