*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root{
    display: flex;
    justify-content: center;
}

.App{
    width: 800px;
}

.post{
    display: flex;
    padding: 15px;
    border: 2px solid rgb(152, 210, 140);
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
    border-radius: 25px;
}

.post-enter {
transform:translateX(-200px);
}
.post-enter-active {
transform:translateX(0px);
transition: all 500ms ease-in;
}
.post-exit {
opacity: 1;
}
.post-exit-active {
transform:translateX(-600px);
transition: all 500ms ease-in;
}


.post__btns{
    display: flex;
}

.page__wrapper{
    display: flex;
    margin: 30px 0;
    align-items: center;
    justify-content: center;
}

.page{
    margin: 10px;
    padding: 10px;
    border: 1px solid rgb(152, 210, 140);
    border-radius:25px;
    cursor: pointer;
}

.page__current{
    border: 2px solid rgb(70, 128, 204);
    font-weight: bold;
    
}

.nav{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    list-style-type: none;
    margin: 30px 0;
    padding: 15px;
    text-decoration: none;

}

.nav__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;

}

.nav__wrapper{
    width: 100vh;
}

.nav li {
    margin: 0 15px;
    text-decoration: none;
}
.nav li a{
    text-decoration: none;
    color: black;
}
.nav li .active {
  font-weight: bold;
  color: green;
}

.post{
    display: flex;
    flex-direction: column;
    max-width: 800px;
    margin-bottom: 50px;
}

.post .post__content{
    margin: 30px;
}

.form__wrap{
    display: flex;
    align-items: center;
    justify-content:center;
    flex-direction: column;
    width: 400px;
    margin: 0 auto;
}